import React from "react";

const HISTORY_L = 23;

export class History extends React.PureComponent {

    state = {
        n: 0
    };

    push = imd => {

        const {value, onChange} = this.props;

        const newV = push(HISTORY_L + 1)(value, imd, this.state.n + 1);

        onChange && onChange(newV);

        this.setState({n: newV.length - 1});
    };


    undo = e => {
        if (this.state.n === 0) return;

        this.setState(state => ({
            n: state.n - 1
        }), () => {
            const {value, onDo} = this.props;

            onDo && onDo(value[this.state.n]);
        });
    };
    redo = e => {
        if (this.state.n === this.props.value.length - 1) return;

        this.setState(state => ({
            n: state.n + 1
        }), () => {
            const {value, onDo} = this.props;

            onDo && onDo(value[this.state.n]);
        });
    };

    render() {
        return (
            <>
                <button onClick={this.undo} disabled={this.state.n === 0}>undo</button>
                <button onClick={this.redo} disabled={this.state.n === this.props.value.length - 1}>redo</button>
            </>
        );
    }

}


export const push = lenght => (array, item, n) => {
    let newA = [...array.slice(0, n), item];
    if (newA.length > lenght)
        newA.shift();
    return newA;
};