import React from 'react';
import {BrushTypes, Fills} from "./utils";
import {Select} from "./Select";
import {MoveHandlers} from "./MoveHandlers";

export class Brush extends React.PureComponent {

    setBrushSize = size => {
        const {onChange, value} = this.props;

        onChange && onChange({
            ...value,
            size
        });
    };

    handleSizeChange = e => {
        this.setBrushSize(+e.target.value);
    };

    setOpacity = opacity => {
        const {onChange, value} = this.props;

        onChange && onChange({...value, opacity});
    };

    handleOpacityChang = e => {
        this.setOpacity(+e.target.value);
    };

    selectType = type => {
        const {onChange, value} = this.props;

        onChange && onChange({...value, type});
    };

    render() {
        const {value, min = 1, max = 100} = this.props;
        return (
            <div className={"brush"}>
                <div>brush</div>
                <div className={"brushbody"}>
                    <input type="range" min={min} max={max} value={value.size} onChange={this.handleSizeChange}/>
                    <MoveHandlers
                        text={"(f)"}
                        codes={[70, 102, 1072, 1040]}
                        value={value.size}
                        onChange={this.setBrushSize}
                        step={1}
                        min={min} max={max}/>
                    <input
                        type="range"
                        min={0} max={1} step={0.05}
                        value={value.opacity}
                        onChange={this.handleOpacityChang}/>
                    <MoveHandlers
                        text={"(g)"}
                        codes={[71, 103]}
                        value={value.opacity}
                        step={0.01}
                        onChange={this.setOpacity}
                        min={0} max={1}/>
                    <Select
                        items={Object.values(BrushTypes)}
                        value={value.type}
                        onChange={this.selectType}/>
                </div>

            </div>
        );
    }
}