import React from "react";
import {MoveHandlers} from "./MoveHandlers";

export class Line extends React.PureComponent {

    setBrushSize = size => {
        const {onChange, value} = this.props;

        onChange && onChange({
            ...value,
            size
        });
    };

    handleSizeChange = e => {
        this.setBrushSize(+e.target.value);
    };

    setOpacity = opacity => {
        const {onChange, value} = this.props;

        onChange && onChange({...value, opacity});
    };

    handleOpacityChang = e => {
        this.setOpacity(+e.target.value);
    };

    render() {
        const {value, min, max} = this.props;
        return (
            <div className={"line"}>
                <div>line</div>
                <div className={"brushbody"}>
                    <input type="range" min={1} max={100} value={value.size} onChange={this.handleSizeChange}/>
                    <MoveHandlers
                        text={"(f)"}
                        codes={[70, 102, 1072, 1040]}
                        value={value.size}
                        onChange={this.setBrushSize}
                        step={0.5}
                        min={min} max={max}/>
                    <input
                        type="range"
                        min={0} max={1} step={0.05}
                        value={value.opacity}
                        onChange={this.handleOpacityChang}/>
                    <MoveHandlers
                        text={"(g)"}
                        codes={[71, 103]}
                        value={value.opacity}
                        step={0.01}
                        onChange={this.setOpacity}
                        min={0} max={1}/>
                </div>
            </div>
        );
    }
}