import * as React from 'react';
import {Canvas} from "./Canvas";
import {createImage, ResizeType} from "./utils";

const nextType = {
    no: "nn",
    nn: "bi",
    bi: "no"
};

export class PatternEditor extends React.Component {

    constructor(props){
        super(props);


        this.state = {
            mask: true,
            w: props.value.w,
            h: props.value.h,
        };

    }

    componentWillReceiveProps(nextProps) {
        if (this.props.value.w !== nextProps.value.w)
            this.setState({w: nextProps.value.w});
        if (this.props.value.h !== nextProps.value.h)
            this.setState({h: nextProps.value.h})
    }

    componentDidMount() {
        this.updateMaskedImage();
    }

    updateMaskedImage = () => {
        try {
            const {onChange, value} = this.props;

            const ctx = this.refs["canvasMasked"].getCtx();

            ctx.putImageData(this.refs["canvasMask"].getImageData(), 0, 0);
            ctx.globalCompositeOperation = "source-in";
            ctx.drawImage(this.refs.canvas.canvas, 0, 0, value.w, value.h);


            createImage(ctx.canvas).then(img => onChange && onChange({...this.props.value, imageMasked: img}));
            createImage(this.refs["canvasMask"].canvas).then(img => onChange && onChange({
                ...this.props.value,
                imageMask: img
            }));

        } catch (e) {
        }
    };

    maskToggle = e => this.setState(state => ({mask: !state.mask}));

    sizeChangeHandler = s => e => {
        const {onChange, value} = this.props;
        const size = +e.target.value;

        this.setState({[s]: e.target.value});

        if (size < 1) return;
        onChange && onChange({...value, [s]: size});
        this.updateMaskedImage();
    };
    mirrorChangeHandler = name => e => {
        const {onChange, value} = this.props;
        onChange && onChange({...value, [name]: !value[name]});
    };
    imageChangeHandler = image => {
        const {onChange, value} = this.props;

        onChange && onChange({...value, image});
        this.updateMaskedImage();
    };
    maskChangeHandler = mask => {
        const {onChange, value} = this.props;
        onChange && onChange({...value, mask});
        this.updateMaskedImage();
    };
    resizeTypeChangeHandler = e => {
        const {onChange, value} = this.props;
        onChange && onChange({...value, resizeType: nextType[value.resizeType]});
    };

    handleFile = e => {
        var reader = new FileReader();
        reader.onload = (event) => {
            var img = new Image();
            img.onload = () => {
                this.refs.canvas.ctx.drawImage(img, 0, 0, this.props.value.w, this.props.value.h);
                this.updateMaskedImage();
            };
            img.src = event.target.result;
        };
        reader.readAsDataURL(e.target.files[0]);
    };

    render() {
        const {value, onPatternDraw, onMaskDraw, onPatternClick, onMaskClick, onRemove, chosen, onChose} = this.props;
        return (
            <div className={"patternEditor"}>
                <div className={value.w < 63 ? "canvases" : "canvases-col"}>
                    <Canvas ref="canvas" resize={ResizeType[value.resizeType]}
                            value={value.image}
                            width={value.w} height={value.h}
                            onDraw={onPatternDraw}
                            onClick={onPatternClick}
                            onChange={this.imageChangeHandler}/>
                    <div style={{display: !this.state.mask ? "none" : ""}}>
                        <Canvas ref="canvasMask"
                                resize={ResizeType[value.resizeType === "bi" ? "nn" : value.resizeType]}
                                value={value.mask}
                                width={value.w} height={value.h}
                                onDraw={onMaskDraw}
                                onClick={onMaskClick}
                                onChange={this.maskChangeHandler}/>
                    </div>
                    <div style={{display: !this.state.mask ? "none" : ""}}>
                        <Canvas ref="canvasMasked"
                                readonly
                                resize={ResizeType[value.resizeType === "bi" ? "nn" : value.resizeType]}
                                value={value.masked}
                                width={value.w} height={value.h}/>
                    </div>
                </div>
                <div className={"btns"}>
                    <button onClick={onChose} style={{
                        visibility: chosen ? "visible" : ""
                    }}>c
                    </button>
                    <button onClick={this.maskToggle}>{!this.state.mask ? "show " : "hide "}mask</button>
                    <br/>
                    <input type="number" step={1} min={1} max={500} value={this.state.w}
                           onChange={this.sizeChangeHandler('w')}/>
                    <input type="number" step={1} min={1} max={500} value={this.state.h}
                           onChange={this.sizeChangeHandler('h')}/>
                    stretch:
                    <button onClick={this.resizeTypeChangeHandler} className={'resizeType'}>{value.resizeType}</button>
                    <br/>
                    mirror:x
                    <input type="checkbox" value={value.xMirror} onChange={this.mirrorChangeHandler('xMirror')}/>
                    y
                    <input type="checkbox" value={value.yMirror} onChange={this.mirrorChangeHandler('yMirror')}/>
                    <br/>
                    <input type="file" id="imageLoader" name="imageLoader" onChange={this.handleFile}/>
                    {/*<button onClick={onRemove}>remove</button> todo: !!! багг */}
                </div>
            </div>
        );
    }
}