import React from 'react';
import {Fills, Tools} from "./utils";
import {Select} from "./Select";
import {MoveHandlers} from "./MoveHandlers";

export class Paint extends React.PureComponent {

    handlePaintFillChose = fill => {
        const {onChange, value} = this.props;

        onChange && onChange({
            ...value,
            fill
        });
    };
    handleToleranceChang = e => {
        this.setTolerance(+e.target.value);
    };

    setTolerance = tolerance => {
        const {onChange, value} = this.props;

        onChange && onChange({
            ...value,
            tolerance
        });
    };

    render() {
        const {value} = this.props;
        return (
            <div className={"paint"}>
                <div>paint</div>
                <div className={"brushbody"}>
                    <input
                        type="range"
                        min={0} max={255} step={1}
                        value={value.tolerance}
                        onChange={this.handleToleranceChang}/>
                    <MoveHandlers
                        text={"(h)"}
                        codes={[72, 104]}
                        value={value.tolerance}
                        onChange={this.setTolerance}
                        step={0.5}
                        min={0} max={255}/>
                    <Select
                        items={Object.values(Fills)}
                        value={value.fill}
                        onChange={this.handlePaintFillChose}/>
                </div>
            </div>
        );
    }
}