import React from "react";

export class Enter extends React.PureComponent {
    order = [2, 1, 2, 1, 2];
    state = {
        i: 0,
        open: false
    };
    handleMouseEnter = i => e => {
        if (i === this.order[this.state.i]) {
            // console.log(i + 1, this.order[this.state.i]);
            this.setState(state => ({i: state.i + 1}), () => {
                if (this.state.i === this.order.length) {
                    this.setState({open: true});
                    this.setState({i: 0})
                }
            })
        } else {
            if (i !== this.order[0]) {
                this.setState(state => ({i: 0}))
            } else {

                this.setState(state => ({i: 1}))
            }

        }
    };

    render() {
        return (
            this.state.open ? this.props.children :
                <div className={"enter"}>
                    {[0, 1, 2, 3, 4].map(i => (
                        <div
                            key={i}
                            className={"enter-door"}
                            onMouseEnter={this.handleMouseEnter(i)}/>
                    ))}
                </div>
        );
    }

}