import React from 'react';
import {BrushTypes, Fills} from "./utils";
import {Select} from "./Select";
import {MoveHandlers} from "./MoveHandlers";

export class Pattern extends React.PureComponent {

    setBrushSize = size => {
        const {onChange, value} = this.props;

        onChange && onChange({
            ...value,
            size
        });
    };

    handleSizeChange = e => {
        this.setBrushSize(+e.target.value);
    };

    setOpacity = opacity => {
        const {onChange, value} = this.props;

        onChange && onChange({...value, opacity});
    };

    handleOpacityChang = e => {
        this.setOpacity(+e.target.value);
    };

    selectType = type => {
        const {onChange, value} = this.props;

        onChange && onChange({...value, type});
    };

    render() {
        const {onNext, onPrev, value, patterns} = this.props;
        return (
            <div className={"brush"}>
                <div>pattern</div>
                <div className={"brushbody"}>
                    {patterns.length === 0 && <div>none</div>}
                    {patterns.length === 1 && value}
                    {patterns.length > 1 && <>
                        <div className={"patternVal"}> {value} </div>
                        <div>
                            <button onClick={onNext} className={'moveHandlerTip'}>{"<"}</button>
                        </div>
                        <div>
                            <button onClick={onPrev} className={'moveHandlerTip'}>{">"}</button>
                        </div>
                    </>}
                </div>

            </div>
        );
    }
}