import React from 'react';
import {throttled} from "./utils";

export class MoveHandlers extends React.Component {

    state = {
        pressed: false
    };

    createMouseHandler = () => {
        let startE = null;
        let startValue = this.props.value;
        return throttled(50, e => {
            if (!startE) {
                startE = e;
                return;
            }

            let value = startValue + this.props.step * (e.x - startE.x);
            if (value > this.props.max) value = this.props.max;
            if (value < this.props.min) value = this.props.min;

            this.props.onChange(value);
        })
    };

    keydownHandler = e => {
        const code = e.key.charCodeAt(0);

        if (this.props.codes.indexOf(code) !== -1) {
            if (!this.state.pressed) {
                this.setState(state => ({pressed: true}));
                this.handler = this.createMouseHandler();
                document.addEventListener("mousemove", this.handler);
            }
        }
    };

    keyupHandler = (e) => {
        const code = e.key.charCodeAt(0);

        if (this.props.codes.indexOf(code) !== -1) {
            if (this.state.pressed) {
                this.setState(state => ({pressed: false}));
                document.removeEventListener("mousemove", this.handler);
                this.handler = null;
            }
        }

    };


    componentDidMount() {
        document.addEventListener("keydown", this.keydownHandler);
        document.addEventListener("keyup", this.keyupHandler);

    }

    componentWillUnmount() {
        document.removeEventListener("keydown", this.keydownHandler);
        document.removeEventListener("keyup", this.keyupHandler);
        this.handler && document.removeEventListener("mousemove", this.handler);

    }

    render() {
        const {hide, text} = this.props;
        return (
            hide ? null : <button className={"moveHandlerTip"}>{text}{this.state.pressed ? "*" : ""}</button>
        );
    }
}